import React, { Children, useState } from 'react';
import Modal from 'react-modal';
import { confirmAlert } from 'react-confirm-alert';
import { AdjustLoan } from 'components/common/Form/AdjustLoan';
import { CloseModal } from 'assets';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { statuses, actionTypes } from 'config/constants';
import {
  ModalHeader,
  ModalContent,
  ModalContentDetails,
  BtnGrpLoanModal,
  ModalButton,
  ConfirmAlertWrapper,
  ConfirmAlertBtnWrapper,
  BtnGrpLoanModalWrapper,
  ModalActionsContent,
  ModalActionDetails,
  ModalUserActions,
  ModalActionDetailsContent,
  LoanDetailsRow,
  LoanDetailsRowTitle,
  LoanDetailsRowValue
} from './ModalComponents';

const FirstCustomStyles = {
  overlay: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    top: '0',
    left: '0',
    right: '0',
    bottom: '0'
  },
  content: {
    position: 'relative',
    maxWidth: '831px',
    width: '100%',
    minHeight: '454px',
    //height: '326px',
    height: 'auto',
    border: 'none',
    boxShadow: '0px 1px 4px 1px rgba(0, 0, 0, 0.12)',
    borderRadius: '12px',
    padding: '0',
    top: '0',
    left: '0',
    right: '0',
    bottom: '0'
  }
};
const AdjustPositionStyles = {
  overlay: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    top: '0',
    left: '0',
    right: '0',
    bottom: '0'
  },
  content: {
    position: 'relative',
    maxWidth: '831px',
    width: '100%',
    minHeight: '454px',
    //minHeight: '326px',
    height: 'auto',
    border: 'none',
    boxShadow: '0px 1px 4px 1px rgba(0, 0, 0, 0.12)',
    borderRadius: '12px',
    padding: '0',
    top: '0',
    left: '0',
    right: '0',
    bottom: '0'
  }
};

Modal.setAppElement('#root');

export default function LoanModal({
  modalIsOpen,
  closeModal,
  path,
  status,
  isShareholder,
  accruedInterest,
  canBeForeclosed,
  approveLoan,
  closeLoan,
  adjustLoan,
  withdrawInterest,
  forecloseLoan,
  newCollateralRatio,
  calcNewCollateralRatio,
  interestPaid,
  collateralTypeName,
  remainingLoan, 
  collateralRatio, 
  collateralAmount,
  cryptoFromLenderName
}) {
  const [adjustPosition, adjustPositionToggle] = useState(false);
  const loanStatusPending = status === statuses['Pending'].status;

  const confirm = (type) => {
    confirmAlert({
      customUI: ({ onClose }) => {
        return (
          <ConfirmAlertWrapper>
            <h2>{actionTypes[type].confirmationText}</h2>
            {/*{type === 'WithdrawInterest' && (
              <h5>Accrued Interest: {accruedInterest + ' ' + collateralTypeName}</h5>
            )}*/}
            <ConfirmAlertBtnWrapper>
              <ModalButton onClick={onClose}
                btnColor="rgba(35,69,102,0.7)"    
                backgroundColor="#EAEAEA"
              >No
              </ModalButton>
              <ModalButton
                btnColor="rgba(35,69,102,0.7)"    
                backgroundColor="#EAEAEA"
                confirmBtn={true}
                onClick={() => {
                  controlAction(type, onClose);
                }}
              >
                Yes
              </ModalButton>
            </ConfirmAlertBtnWrapper>
          </ConfirmAlertWrapper>
        );
      }
    });
  };

  const controlAction = (type, onClose) => {
    if (type === actionTypes['Cancel'].name || type === actionTypes['Close'].name) {
      closeLoan();
      closeModal();
      onClose();
    } else if (type === actionTypes['Approve'].name) {
      approveLoan();
      closeModal();
      onClose();
    } else if (type === actionTypes['WithdrawInterest'].name) {
      withdrawInterest();
      closeModal();
      onClose();
    } else if (type === actionTypes['Foreclose'].name) {
      forecloseLoan();
      closeModal();
      onClose();
    }
  };

  const modalClose = () => {
    closeModal();
    adjustPositionToggle(false);
  };

  const borrowModal = () => {
    return (
      <div>
        {!adjustPosition ? (
          <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            style={FirstCustomStyles}
            shouldCloseOnOverlayClick={false}
            contentLabel='Adjust'
          >
            <ModalHeader>
              <h2>Manage Loan Request</h2>
              <button onClick={() => modalClose()}>
                <img src={CloseModal} alt='' />
              </button>
            </ModalHeader>

            <ModalActionsContent>


              <ModalActionDetails>

                <ModalActionDetailsContent>
                  <LoanDetailsRow>
                    <LoanDetailsRowTitle>
                      Loan amount
                    </LoanDetailsRowTitle>

                    <LoanDetailsRowValue>
                      {remainingLoan} {cryptoFromLenderName}
                    </LoanDetailsRowValue>

                  </LoanDetailsRow>

                  <LoanDetailsRow>
                    <LoanDetailsRowTitle>
                      Collateral amount
                    </LoanDetailsRowTitle>

                    <LoanDetailsRowValue>
                      {collateralAmount} {collateralTypeName}
                    </LoanDetailsRowValue>

                  </LoanDetailsRow>

                  <LoanDetailsRow>
                    <LoanDetailsRowTitle>
                      Collateral ratio
                    </LoanDetailsRowTitle>

                    <LoanDetailsRowValue>
                      {collateralRatio}%
                    </LoanDetailsRowValue>

                  </LoanDetailsRow>

                  {/*<div>
                    <h2>
                      <span>APY</span>
                    </h2>
                    <h2>
                      {interestPaid} {collateralTypeName}
                    </h2>
                  </div>*/}
                </ModalActionDetailsContent>

              </ModalActionDetails>

              <ModalUserActions>

                <ModalContent>           
                    <BtnGrpLoanModal>

                      <BtnGrpLoanModalWrapper>
                        <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                        <ModalButton
                          disabled={loanStatusPending}
                          onClick={() => adjustPositionToggle(true)}
                          grayBtn={true}
                          backgroundColor="#EAEAEA"
                          btnColor="#234566"
                        >
                          Adjust Collateral
                        </ModalButton>
                      </BtnGrpLoanModalWrapper>

                      <BtnGrpLoanModalWrapper>       
                        <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>                 
                        <ModalButton onClick={() => confirm('Close')} backgroundColor="#0A66E1" btnColor="#FFFFFF">Close Loan</ModalButton>
                      </BtnGrpLoanModalWrapper>

                    </BtnGrpLoanModal>
                </ModalContent>
              </ModalUserActions>


            </ModalActionsContent>
          </Modal>
        ) : (
          <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            style={AdjustPositionStyles}
            shouldCloseOnOverlayClick={false}
            contentLabel='AdjustPosition'
          >
            <ModalHeader>
              <h2>Adjust Position</h2>
              <button onClick={() => modalClose()}>
                <img src={CloseModal} alt='' />
              </button>
            </ModalHeader>
            <AdjustLoan
              collateralTypeName={collateralTypeName}
              adjustLoan={adjustLoan}
              remainingLoan={remainingLoan}
              cryptoFromLenderName={cryptoFromLenderName}
              collateralAmount={collateralAmount}
              newCollateralRatio={newCollateralRatio}
              collateralRatio={collateralRatio}
              calcNewCollateralRatio={calcNewCollateralRatio}
            />
          </Modal>
        )}
      </div>
    );
  };

  const earnModal = () => {
    return (
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={FirstCustomStyles}
        shouldCloseOnOverlayClick={false}
        contentLabel='Adjust'
      >
        <ModalHeader>
          <h2>
            Manage Earning Asset
          </h2>
          <button onClick={() => modalClose()}>
            <img src={CloseModal} alt='' />
          </button>
        </ModalHeader>
        
        <ModalActionsContent>
         <ModalActionDetails>

          <ModalActionDetailsContent>
            <LoanDetailsRow>
              <LoanDetailsRowTitle>
                Loan amount
              </LoanDetailsRowTitle>

              <LoanDetailsRowValue>
                {remainingLoan} {cryptoFromLenderName}
              </LoanDetailsRowValue>

            </LoanDetailsRow>

            <LoanDetailsRow>
              <LoanDetailsRowTitle>
                Collateral amount
              </LoanDetailsRowTitle>

              <LoanDetailsRowValue>
                {collateralAmount} {collateralTypeName}
              </LoanDetailsRowValue>

            </LoanDetailsRow>

            <LoanDetailsRow>
              <LoanDetailsRowTitle>
                Collateral ratio
              </LoanDetailsRowTitle>

              <LoanDetailsRowValue>
                {collateralRatio}%
              </LoanDetailsRowValue>

            </LoanDetailsRow>

            {/*<div>
              <h2>
                <span>APY</span>
              </h2>
              <h2>
                {interestPaid} {collateralTypeName}
              </h2>
            </div>*/}
          </ModalActionDetailsContent>

          </ModalActionDetails>
          <ModalUserActions>
        <ModalContent>
          <BtnGrpLoanModal>
            {status === statuses['Pending'].status ? (
              <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>

                <ModalButton
                  onClick={() => confirm('Approve')}
                  btnColor="#ffffff"
                  backgroundColor="#2ECC71"
                >
                  Accept Loan request
                </ModalButton>
              </BtnGrpLoanModalWrapper>


            ) : status === statuses['Active'].status ? (

              <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                <ModalButton
                  onClick={() => confirm('WithdrawInterest')}
                  btnColor="#234566"    
                  backgroundColor="#EAEAEA"
                >
                  Withdraw Interest
                </ModalButton>
              </BtnGrpLoanModalWrapper>


            ) : status === statuses['Under_Collateralized'].status ? (

              <BtnGrpLoanModal>

              <BtnGrpLoanModalWrapper>
                  <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('WithdrawInterest')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                    display={!isShareholder ? 'none' : ''}
                  >
                    Withdraw Interest
                  </ModalButton>
                </BtnGrpLoanModalWrapper>


                <BtnGrpLoanModalWrapper>
                  <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('Foreclose')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                  >
                    Foreclose Loan
                  </ModalButton>
                </BtnGrpLoanModalWrapper>



              </BtnGrpLoanModal>
            ) : status === statuses['At_Risk'].status ? (
              <BtnGrpLoanModal>


                <BtnGrpLoanModalWrapper>
                  <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('WithdrawInterest')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                    display={!isShareholder ? 'none' : ''}
                  >
                    Withdraw Interest
                  </ModalButton>
                </BtnGrpLoanModalWrapper>



                <BtnGrpLoanModalWrapper>
                  <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('Foreclose')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                  >
                    Foreclose Loan
                  </ModalButton>
                </BtnGrpLoanModalWrapper>



              </BtnGrpLoanModal>
            ) : status === statuses['Foreclosing'].status ? (
              <BtnGrpLoanModal>


              <BtnGrpLoanModalWrapper>
                  <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('WithdrawInterest')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                    display={!isShareholder ? 'none' : ''}
                  >
                    Withdraw Interest
                  </ModalButton>
                </BtnGrpLoanModalWrapper>



                <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                  <ModalButton
                    onClick={() => confirm('Foreclose')}
                    btnColor="#234566"    
                    backgroundColor="#EAEAEA"
                    disabled={!canBeForeclosed}
                  >
                    Foreclose Loan
                  </ModalButton>
                </BtnGrpLoanModalWrapper>




              </BtnGrpLoanModal>
            ) : status === statuses['Foreclosed'].status ? (



              <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                <ModalButton
                  onClick={() => confirm('WithdrawInterest')}
                  btnColor="#234566"    
                  backgroundColor="#EAEAEA"
                >
                  Withdraw Interest
                </ModalButton>
              </BtnGrpLoanModalWrapper>




            ) : status === statuses['Early_closing'].status ? (



              <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                <ModalButton
                  onClick={() => confirm('WithdrawInterest')}
                  btnColor="#234566"    
                  backgroundColor="#EAEAEA"
                >
                  Withdraw Interest
                </ModalButton>
              </BtnGrpLoanModalWrapper>




            ) : status === statuses['Closing'].status ? (



              <BtnGrpLoanModalWrapper>
                <h2>Lorem ipsum dolor sit amet consectetur adipisicing elit.</h2>
                <ModalButton
                  onClick={() => confirm('WithdrawInterest')}
                  btnColor="#234566"    
                  backgroundColor="#EAEAEA"
                >
                  Withdraw Interest
                </ModalButton>
              </BtnGrpLoanModalWrapper>





            ) : (
              ''
            )}
          </BtnGrpLoanModal>
        </ModalContent>
        </ModalUserActions>
        </ModalActionsContent>
      </Modal>
    );
  };

  return path === 'borrow' ? borrowModal() : path === 'earn' ? earnModal() : false;
}
